// TODO: Uncomment after updating 'Work' â€” import { Link } from 'gatsby-link';

import { PageProps as P } from 'gatsby';

import React, { useEffect, useState } from 'react';
import Typist from 'react-typist';

import { PageProps } from './page';
import { useMetadata } from './useMetadata';

import './index.scss';

const DEFAULT_TYPING = false;

export function Layout({
  children,
  pageContext: {
    frontmatter: { callout, description },
  },
}: P<{}, PageProps>) {
  const [typing, setTyping] = useState(DEFAULT_TYPING);

  function onTypingDone() {
    setTyping(DEFAULT_TYPING);
  }

  useEffect(() => setTyping(true));

  const { contact, name, role } = useMetadata();

  return (
    <>
      <header>
        <h1>
          {name}

          <div className="role">{role}</div>
        </h1>

        {/* TODO: Uncomment after updating 'Work'.
  
              <nav aria-label="Website">
                <ul>
                  {[
                    {
                      children: 'About',
                      to: '/',
                    },
                    {
                      children: 'Work',
                      to: '/work',
                    },
                  ].map(({ children, to }) => (
                    <li key={`header__li--${to}`}>
                      <Link activeClassName="a--active" to={to}>
                        {children}
                      </Link>
                    </li>
                  ))}
                </ul>
                  </nav> */}
      </header>

      <main>
        <section className="hero">
          <h2>
            {typing && (
              <Typist
                avgTypingDelay={50}
                cursor={{ element: '_' }}
                onTypingDone={onTypingDone}
              >
                {callout.map((item, index) => (
                  <span
                    key={`callout--${item}`}
                    className={`callout--${index % 2 ? 'odd' : 'even'}`}
                  >
                    {item}

                    <Typist.Backspace count={item.length} delay={500} />
                  </span>
                ))}
              </Typist>
            )}
          </h2>

          <p className="description">{description}</p>
        </section>

        {children}
      </main>

      <footer>
        <nav aria-label="Contact">
          <ul>
            {contact.map(({ children, href }) => (
              <li key={`footer__li--${children}`}>
                <a href={href} rel="noopener noreferrer" target="_blank">
                  {children}
                </a>
              </li>
            ))}
          </ul>
        </nav>
      </footer>
    </>
  );
}
