import { graphql, useStaticQuery } from 'gatsby';

let metadata;

export function useMetadata() {
  if (!metadata) {
    metadata = useStaticQuery(graphql`
      query {
        site {
          siteMetadata {
            contact {
              children
              href
            }
            meta
            name
            role
          }
        }
      }
    `).site.siteMetadata;
  }

  return metadata;
}
